<template>
  <div class="edit">
    <edit-nav-bar/>
    <save-panel v-show="show" @on-click-hide="hide"/>
    <el-row style="height:100%;padding: 0 10px;">
      <el-col :span="24" class="edit-left-col">
        <mark-down @on-save="save"/>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import MarkDown from 'vue-meditor';
import EditNavBar from './EditNavBar.vue'
import SavePanel from './SavePanel.vue'
export default {
  components: {
    MarkDown,
    EditNavBar,
    SavePanel
  },
  data () {
    return {
      show: false,
      isNewNotes: true
    }
  },
  methods:{
    save(res){
      if(this.isNewNotes){
        this.isNewNotes = false
        this.show = true
      }
      console.log(res)
    },
    hide(){
      this.show = false;
    }
  }
}
</script>


<style scoped lang="scss">
.edit{
  height: 100%;
  width: 100%;
  background-color: #fff;
  .edit-left-col{
    border-left:1px solid rgba(64, 158, 255,.2);
    border-right:1px solid rgba(64, 158, 255,.2);
  }
}
</style>


