<template>
  <div class="panel">
    <div class="save-panel" :class="{'save-panel-full':isFullScreen}">
      <div class ="save-panel-header" @dblclick="fullScreen">
        笔记目录
        <i class="iconfont panel-full" :class="{'icon-suoxiao':isFullScreen,'icon-quanping':!isFullScreen}" @click="fullScreen"></i>
        <i class="iconfont icon-guanbi panel-close" @click="$emit('on-click-hide')"></i>
      </div>
      <div class="save-panel-content" :class="{'save-panel-content-full':isFullScreen}">
        <tree class="item" :model="treeData"/>
      </div>
    </div>
    <div class="save-panel-bg" @click="$emit('on-click-hide')"></div>
  </div>
</template>
<script>
export default {
  components: {
  },
  data () {
    return {
      isShow: true,
      isFullScreen: false,
      treeData:[{
          name: 'My Tree',
          children: [
            { name: 'hello',
              children: [
                {
                  name: 'child folder',
                  children: [
                    { name: 'hello' },
                    { name: 'wat' }
                  ]
                },
                { name: 'hello' },
                { name: 'wat' },
                {
                  name: 'child folder',
                  children: [
                    { name: 'hello' },
                    { name: 'wat' }
                  ]
                }
              ]
            },
            { name: 'wat' },
            {
              name: 'child folder',
              children: [
                {
                  name: 'child folder',
                  children: [
                    { name: 'hello' },
                    { name: 'wat' }
                  ]
                },
                { name: 'hello' },
                { name: 'wat' },
                {
                  name: 'child folder',
                  children: [
                    { name: 'hello' },
                    { name: 'wat' }
                  ]
                }
              ]
            }
          ]
      }],
    }
  },
  methods:{
    doHide(){
      this.isShow = false;
    },
    fullScreen(){
      this.isFullScreen = !this.isFullScreen
    }
  }
}
</script>

<style scoped lang="scss">
.panel{
  .save-panel-full{
    height: 100% !important;
    width: 100% !important;
    top: 0 !important;
    left: 0 !important;
    margin: 0 !important;
  }
  .save-panel-content-full{
    max-height: 100% !important;
  }
  .save-panel{
    left: 50%;
    height: 400px;
    width: 600px;
    position: fixed;
    top: 50%;
    z-index: 10;
    margin: -200px -300px;
    background-color: #fff;
    border-radius: 3px;
    .save-panel-header{
      height: 44px;
      border-bottom: 1px solid #e0e0e0;
      line-height: 44px;
      padding-left:10px;
      cursor: pointer;
      .panel-close{
        float: right;
        margin-right: 10px;
        font-size: 22px;
        color: #409eff;
        cursor: pointer;
        border-left: 1px solid #e0e0e0;
        padding-left: 10px;
      }
      .panel-full{
        float: right;
        margin-right: 10px;
        font-size: 20px;
        color: #409eff;
        cursor: pointer;
        animation:animenter 1s .1s ease both;
        -webkit-animation:animenter 1s .1s ease both;
	      -moz-animation:animenter 1s .1s ease both;
      }
      .panel-full:hover{
        animation:animleave 1s .1s ease both;
        -webkit-animation:animleave 1s .1s ease both;
	      -moz-animation:animleave 1s .1s ease both;
      }
      @-webkit-keyframes animenter {
        0% {
          -webkit-transform:rotate(0deg)
        }
        100% {
          -webkit-transform:rotate(180deg)
        }
      }
      @-webkit-keyframes animleave {
        0% {
          -webkit-transform:rotate(0deg)
        }
        100% {
          -webkit-transform:rotate(-180deg)
        }
      }
    }
    .save-panel-content{
      padding: 10px;
      max-height: 320px;
      overflow-y: scroll;
    }
    .save-panel-fot{
      height: 44px;
      border-bottom: 1px solid #e0e0e0;
      line-height: 44px;
      padding-left:10px;
    }
  }
  .save-panel:hover{
    box-shadow: 0 0px 10px 3px rgba(64, 158, 255,.9);
  }
  .save-panel-bg{
    background-color: rgba(0,0,0,.3);
    position: fixed;
    top: 0;
    width: 100%;
    height:100%;
    z-index:9;
  }
}

</style>


