<template>
  <div class="read">
    <nav-bar/>
    <el-row>
      <el-col :span="8">
        <div style="padding:20px">
          <tree class="item" :model="treeData"/>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import NavBar from '../../components/navBar.vue'
export default {
  components: {
    NavBar
  },
  data () {
    return {
      treeData:[{
          name: 'My Tree',
          children: [
            { name: 'hello',
              children: [
                {
                  name: 'child folder',
                  children: [
                    { name: 'hello' },
                    { name: 'wat' }
                  ]
                },
                { name: 'hello' },
                { name: 'wat' },
                {
                  name: 'child folder',
                  children: [
                    { name: 'hello' },
                    { name: 'wat' }
                  ]
                }
              ]
            },
            { name: 'wat' },
            {
              name: 'child folder',
              children: [
                {
                  name: 'child folder',
                  children: [
                    { name: 'hello' },
                    { name: 'wat' }
                  ]
                },
                { name: 'hello' },
                { name: 'wat' },
                {
                  name: 'child folder',
                  children: [
                    { name: 'hello' },
                    { name: 'wat' }
                  ]
                }
              ]
            }
          ]
      }]
    }
  },
  methods:{
    doRouter(f){
      
    }
  }
}
</script>


<style scoped lang="scss">
.read{
  height: 100%;
  width: 100%;
}
</style>


