<template>
  <el-row>
    <div class="nav-bar">
        <el-col :span="10" style="height:100%">
          <ul class="menu-left">
            <li>
              <div><span style="font-weight: bold;">{{noteTitle}}</span><span style="font-size:12px">[{{noteStatus}}{{noteSize}}]</span></div>
              <div style="font-size:14px;cursor: pointer;">
                <router-link to="/tree">
                  <i class="iconfont icon-liucheng"></i>
                </router-link>
                <span v-for="(item,index) in noteNodes" :key="index">
                  <router-link :to="item.url">
                  {{item.name}}/
                  </router-link>
                </span>
              </div>
            </li>
          </ul>
        </el-col>
        <el-col :span="6" style="border-right:1px solid #e0e0e0;height:100%">
          <ul class="menu-center">
            <li>保存</li>
            <li>发布</li>
          </ul>
        </el-col>
        <el-col :span="8">
          <ul class="menu-right">
            <router-link to="/read">
              <li><i class="iconfont icon-bangzhuzhongxin"></i>笔记中心</li>
            </router-link>
            <router-link to="/edit">
              <li><i class="iconfont icon-console"></i>编辑台</li>
            </router-link>
             <router-link to="/tree">
              <li><i class="iconfont icon-liucheng"></i>笔记树</li>
            </router-link>
          </ul>
        </el-col>
    </div>
  </el-row>
</template>
<script>
  export default {
    data() {
      return {
        noteTitle:'Vue学习笔记',
        noteStatus: '编辑中..',
        noteSize: '20万字',
        noteNodes: [{
          name:'JavaScript',
          url:'/read',
        },{
          name:'第一章',
          url:'/tree'
        }]
      };
    }
  }
</script>
<style scoped lang="scss">
.nav-bar{
  height: 60px;
  border-bottom: 1px solid rgba(64, 158, 255,.9);
  background-color: #ffffff;
  i{
    font-size: 20px;
    top: 3px;
    position: relative;
    margin-right: 3px;
  }
  li{
    list-style: none;
    float: left;
    margin-left: 20px;
    color: #409eff;
  }
  .menu-left{
    li{
      margin-top: -5px;
      margin-left: 0px;
    }
  }
  .menu-center{
    li{
      background-color: rgb(64, 158, 255);
      padding: 5px 25px;
      color: #fff;
      border-radius: 5px;
      font-size:14px;
      cursor: pointer;
    }
  }
  .menu-right li{
    cursor: pointer;
  }
  .menu-right li:hover{
    animation:sucaijiayuan 1s .1s ease both;
    -webkit-animation:sucaijiayuan 1s .1s ease both;
	  -moz-animation:sucaijiayuan 1s .1s ease both;
  }
  @-webkit-keyframes sucaijiayuan {
    0% {
      -webkit-transform:scale(1)
    }
    10%,20% {
      -webkit-transform:scale(0.8) rotate(-2deg)
    }
    30%,50%,70%,90% {
      -webkit-transform:scale(1.1) rotate(2deg)
    }
    40%,60%,80% {
      -webkit-transform:scale(1.1) rotate(-2deg)
    }
    100% {
      -webkit-transform:scale(1) rotate(0)
    }
  }
}
</style>


